import { codeEnArr, codeKrArr } from "./conditionCode"


/* 현재 위치정보를 가져와서 위경도를 반환하는 비동기 함수 */
export const fnGetCurrentLocation = () => {
  return new Promise(res => {
    navigator.geolocation.getCurrentPosition(location => {
      let latLng = {
        lat: location.coords.latitude, //위도
        lng: location.coords.longitude, //경도
      }
      res(latLng)
    })//getCurrentPosition
  })//promise
}//fnGetCurrentLocation

/* 위경도를 받아서 주소를 반환하는 비동기 함수 */
export const fnGetAddress = (lat, lng) => {
  return new Promise(res => {
    let geocoder = new window.google.maps.Geocoder;
    let latLng = {
      lat, lng,
    };
    geocoder.geocode({ 'location': latLng }, function (address, status) {
      if (status === "OK") {
        res(address)
      } else {
        res(null)
      }
    });
  })//promise
}//fnGetAddress

/* 위경도 정보를 받아서 날씨 정보 반환하는 함수 */
export const fnGetWeather = (lat, lng) => {
  return new Promise(res => {
    fetch(`https://api.openweathermap.org/data/3.0/onecall?lat=${lat}&lon=${lng}&appid=1053c2b42d08f132c79ab1064561e059`).then((result) => {
      let data = result.json()
      //console.log(data);
      res(data)
    }).catch(() => {
      alert('날씨정보를 받아오지 못 했습니다. 잠시 후 다시 시도해주세요')
      res(null)
    })
  })//promise
}//fnGetWeather

export const fnOutput = (category) => {
  let time = new Date(category.dt * 1000)
  let year = time.getFullYear()
  let month = time.getMonth() + 1
  let date = time.getDate()
  let hour = time.getHours()
  let minute = time.getMinutes()
  minute = (minute < 10)?'0'+minute : minute
  let dayArr = ['SUM', 'MON', 'TUE', 'WED', 'THR', 'FRI', 'SAT',]
  let day = dayArr[time.getDay()]
  let desc = codeKrArr[codeEnArr.indexOf(category.weather[0].description)]
  let temp = (category.temp - 273.15).toFixed(1)
  let tempMin = (typeof (category.temp) === 'object') && (category.temp.min - 273.15).toFixed(1)
  let tempMax = (typeof (category.temp) === 'object') && (category.temp.max - 273.15).toFixed(1)
  let windDeg = category.wind_deg
  let windSpeed = category.wind_speed
  let rain = (category.rain) ? category.rain : 0 //일자별강우량
  rain = (typeof(rain)==='object')? rain['1h']: rain
  let snow = (category.snow) ? category.snow : 0 //일자별적설량
  snow = (typeof(snow)==='object')? snow['1h']: snow
  let sunriseTime = (category.sunrise) && new Date(category.sunrise * 1000)
  let sunriseHour = (category.sunrise) &&  sunriseTime.getHours()
  let sunriseMin = (category.sunrise) &&  sunriseTime.getMinutes()
  let sunsetTime = (category.sunset) && new Date(category.sunset * 1000)
  let sunsetHour = (category.sunset) && sunsetTime.getHours()
  let sunsetMin = (category.sunset) && sunsetTime.getMinutes()
  let icon = category.weather[0].icon
  let feelsLike = (category.feels_like - 273.15).toFixed(1)
  let clouds = category.clouds
  let uvi = category.uvi
  let humidity = category.humidity
  let pressure = category.pressure
  let visibility = (category.visibility / 1000).toFixed(1)
  return ({
    year, month, date, hour, minute, day,
    temp, tempMin, tempMax, desc, windDeg, windSpeed, rain, snow,
    sunriseHour, sunriseMin, sunsetHour, sunsetMin, 
    icon,feelsLike,clouds,uvi,humidity,pressure,visibility
  })
}//


