import React from 'react';
import { fnOutput } from '../store/function';

const CompDay = ({ dayData }) => {
  let { month, date, icon, rain, snow, tempMin, tempMax,windSpeed } = fnOutput(dayData)
  return (
    <li>
      <b>{month}월{date}일</b>
      <img src={`${process.env.PUBLIC_URL}/img/icons/${icon}.gif`} alt="" />
      <b>
        <i className="fa-solid fa-temperature-high"></i>
        {tempMax} <sub>&#8451;</sub>
      </b>
      <b>
        <i className="fa-solid fa-temperature-low"></i>
        {tempMin} <sub>&#8451;</sub>
      </b>
      <b>
      <i className="fa-regular fa-flag"></i>
        {tempMax} <small>m/sec</small>
      </b>
      <b>
        <i className="fa-solid fa-cloud-showers-heavy"></i>
        {rain}mm
      </b>
      <b>
        <i className="fa-solid fa-snowflake"></i>
        {snow}mm
      </b>
    </li>
  );
};

export default CompDay;