import React from 'react';
import { fnOutput } from '../store/function';

const CompHour = ({ hourData }) => {
  let { month, date, hour, minute, icon, rain, snow } = fnOutput(hourData)
  return (
    <li>
      <div>
        <b>{month}월{date}일</b>
        <b>{hour}:{minute}</b>
        <img src={`${process.env.PUBLIC_URL}/img/icons/${icon}.gif`} alt="" />
        <b>
          <i className="fa-solid fa-cloud-showers-heavy"></i>
          {rain}mm
        </b>
        <b>
          <i className="fa-solid fa-snowflake"></i>
          {snow}mm
        </b>
      </div>
    </li>
  );
};

export default CompHour;