import React, { useContext } from 'react';
import { AppContext } from '../App';
import CompHour from './CompHour';
import 'chart.js/auto';
import { Chart } from 'react-chartjs-2';
import ChartDataLabels from 'chartjs-plugin-datalabels';
import { fnOutput } from '../store/function';


const CompHourly = () => {
  let { stWeather } = useContext(AppContext)
  if (!stWeather) return false
  let hourlyArr = stWeather.hourly
  let labelsArr = new Array(24).fill('')

  let dataArr = hourlyArr.map((v, i) => {
    if (i <= 23) {
      let { temp } = fnOutput(v)
      return temp
    }
  })
  dataArr.splice(24, 24)


  const data = {
    labels: labelsArr,
    datasets: [{
      label: 'myGraph',
      data: dataArr,
      borderWidth: 1,
      fill: false,
      borderColor: 'rgb(255, 255, 255)',
      tension: 0.2,
    }]
  }//data

  const options = {
    scales: {
      x: {
        display: false,
        grid: {
          display: false,
        }
      },
      y: {
        display: true,
        beginAtZero: true,
        min: -50,
        max: 50,
        stepSize: 10,
        grid: {
          color: 'rgba(255,255,255,0.2)'
        },
        ticks: {
          display: false,
        }
      },
    },
    plugins: {
      legend: {
        display: false,
        position: 'top',
        labels: {
          color: '#FFF'
        }
      },//legend
      datalabels: {
        display: true,
        color: "white",
        align: 'top',
      }
    },//plugins
    responsive: true,
    maintainAspectRatio: false,
  }//options

  let gridArr = []
  for (let i = 5; i >= -5; i--) {
    gridArr.push(i * 10)
  }


  return (
    <section className='hourly-sectcion'>
      <div className="inner scr-style">
        <h2>
          <i className="fa-regular fa-clock"></i>
          <span>Hourly Forecast</span>
        </h2>
        <div className='hour-cotnainer'>
          <ul>
            {
              hourlyArr.map((v, i) => {
                if (i <= 23) {
                  return <CompHour hourData={v} key={i} />
                }
              })//map
            }
          </ul>
          <div className="chart">
            <Chart type='line' data={data} options={options} plugins={[ChartDataLabels]} />
            <div className="grid">
              {
              gridArr.map((v,i) => {
                return <span key={i}>{v}</span>
              })
              }
            </div>
          </div>{/* chart */}
        </div>{/* hour-cotnainer */}
      </div>{/* inner */}
    </section>
  );
};

export default CompHourly;