import React from 'react';
import { useContext } from 'react';
import { useEffect } from 'react';
import { AppContext } from '../App';
import { fnGetAddress, fnGetCurrentLocation,fnGetWeather } from "../store/function";

const CompHeader = () => {
  let { fnInit,stLatLng, stChangeLatLng, stAddress, stChangeAddress, stWeather, stChangeWeather, } = useContext(AppContext)
  useEffect(() => {
    document.querySelector('.map-btn').addEventListener('click', () => {
      document.querySelector('.map-section').classList.add('active')
    })
    document.querySelector('.refresh-btn').addEventListener('click', async () => {
     let yn = window.confirm('현제 위치로 날씨 정보를 변경하시겠습니까?')
      if(yn){
        fnInit()
        document.querySelector('.app-container').scrollTo({ top: 0 })
      }
    })
  }, [])
  return (
    <header>
      <button className='refresh-btn'>
        <i className="fa-solid fa-arrows-rotate"></i>
      </button>
      <h1>
        <span className='hidden'>날씨앱</span>
        <img src={`${process.env.PUBLIC_URL}/img/main/title.png`} alt="" />
      </h1>
      <button className='map-btn'>
        <i className="fa-solid fa-map-location"></i>
      </button>
    </header>
  );
};

export default React.memo(CompHeader);