import React from 'react';
import { useEffect } from 'react';
import { useContext } from 'react';
import { AppContext } from '../App';
import { fnGetAddress, fnGetWeather } from '../store/function';

const CompMap = () => {
  let {
    stLatLng, stChangeLatLng,
    stAddress, stChangeAddress,
    stWeather, stChangeWeather,
  } = useContext(AppContext)

  useEffect(() => {
    if (stLatLng) {
      let map = new window.google.maps.Map(document.getElementById('map'), {
        center: { lat: stLatLng.lat, lng: stLatLng.lng },
        zoom: 8
      })//map 
      let center = { lat: stLatLng.lat, lng: stLatLng.lng }

      map.addListener('click', async (e) => {
        let latlng = {
          lat: e.latLng.lat(),
          lng: e.latLng.lng()
        }
        let addressArr = await fnGetAddress(latlng.lat, latlng.lng)
        let address
        try {
          address = addressArr[3].formatted_address
        } catch {
          address = '주소를 가져올 수 없습니다'
        }
        let yn = window.confirm(`[${address}]\n해당위치의 날씨정보로 변경하시겠습니까?`)
        if (yn) {
          stChangeLatLng(latlng)
          stChangeAddress(addressArr)
          let weather = await fnGetWeather(latlng.lat, latlng.lng)
          stChangeWeather(weather)
          document.querySelector('.map-section').classList.remove('active')
          document.querySelector('.app-container').scrollTo({ top: 0 })
        }//if yn
      })//click
    }//if
  })//useEffect

  useEffect(() => {
    document.querySelector('.close-map').addEventListener('click', () => {
      document.querySelector('.map-section').classList.remove('active')
    })
  }, [])

  return (
    <section className='map-section'>
      <div id="map"></div>
      <button className='close-map'>
        <i className="fa-solid fa-xmark"></i>
      </button>
    </section>
  );
};

export default CompMap;