import React, { useContext } from 'react';
import { AppContext } from '../App';
import { fnOutput } from '../store/function';

const CompCurrent = () => {
  let { stAddress, stWeather } = useContext(AppContext)
  if (!stAddress || !stWeather) return false
  let current = stWeather.current // 현재날씨 정보객체
  let data = fnOutput(current)//현재시간 날씨정보를 가공해서 반환
  let today = stWeather.daily[0] //오늘날씨 정보객체
  let todyData = fnOutput(today)//오늘날짜별 날씨정보를 가공해서 반환

  /* 일출일몰시간 구현하기 -----------------------*/
  let now = parseInt(data.hour) * 60 + parseInt(data.minute) //현재시간 분 
  let sunStart = parseInt(todyData.sunriseHour) * 60 + parseInt(todyData.sunriseMin) //해뜨는시간 분 
  let sunEnd = parseInt(todyData.sunsetHour) * 60 + parseInt(todyData.sunsetMin) // 해지는시간 분 
  let deg = (now - sunStart) / (sunEnd - sunStart) * 180//(현재시간-해뜨는시간)/(해지는시간-해뜨는시간)*180
  if (deg < 0) deg = 0
  if (deg > 180) deg = 180

  /* 주소 가져오기 */
  let address
  try {
    address = stAddress[3].formatted_address
  } catch {
    address = '확인되지 않는 주소입니다'
  }


  const fnOpenDetail = (e) => {
    e.currentTarget.classList.toggle('active')
    document.querySelector('.detail-section').classList.toggle('active')
  }

  return (
    <section className='current-section'>
      <div className="inner">
        <p className='address'>
          {address}
        </p>{/*address */}
        <div className="current-info">
          <div className="wrap">
            <div className='sun-set-rise'>{/* 일출 일몰 */}
              <div className="circle"></div>
              <time className='sunrise'>
                <i className="fa-solid fa-sun"></i>
                {todyData.sunriseHour}:{todyData.sunriseMin}
              </time>
              <time className='sunset'>
                <i className="fa-solid fa-moon"></i>
                {todyData.sunsetHour}:{todyData.sunsetMin}
              </time>
              <div className='sun' style={{ transform: `rotate(${deg}deg)` }}>
                <i className="fa-regular fa-sun"></i>
              </div>{/*sun*/}
            </div>{/* sun-set-rise */}

            <p className='temp-min-max'>{/* 최저,최고온도 */}
              <b>{todyData.tempMin}<sup>&#176;</sup></b>
              /
              <b>{todyData.tempMax}<sup>&#176;</sup></b>
            </p>

            <p className='temp'>{/* 현재온도 */}
              <img src={`${process.env.PUBLIC_URL}/img/icons/${data.icon}.gif`} alt="" />
              <span>
                <b>{data.temp}</b>
                <sup>&#8451;</sup>
              </span>
            </p>{/* temp */}

            <p className='desc'>{/* 날씨 멘트 */}
              " {data.desc} "
            </p>{/* desc */}
            <p className='rain-snow'>
              <b className='rain'>
                <i className="fa-solid fa-umbrella"></i>
                {data.rain}<small>mm</small>
              </b>{/* rain */}
              <b className='snow'>
                <i className="fa-regular fa-snowflake"></i>
                {data.snow}<small>mm</small>
              </b>
            </p>{/* rain-snow */}
            <p className='wind'>{/* 풍향*/}
              <b className='wind-deg'>
                <i className="fa-solid fa-location-arrow" style={{ transform: `rotate(${data.windDeg - 45}deg)` }}></i>
              </b>
              <b className='wind-speed'>
                <i className="fa-solid fa-wind"></i>
                {data.windSpeed}<small>m/sec</small>
              </b>
            </p>{/*wind*/}
          </div>{/* wrap */}
        </div>{/* current-info */}

        <p className='time'>{/* 시간 과 일자 */}
          <time className='hh-mm'>
            <b>{(data.hour <= 12) ? data.hour : data.hour - 12}:{data.minute}</b>
            <small>{(data.hour <= 12) ? 'AM' : 'PM'}</small>
          </time>
          <time className='yy-dd'>
            <b>{data.year}년 {data.month}월 {data.date}일 </b>
            <small>({data.day})</small>
          </time>
        </p>{/* time */}
      </div>{/* inner */}
      <button className='more' onClick={fnOpenDetail}>
        <i className="fa-solid fa-circle-plus plus"></i>
        <i className="fa-solid fa-circle-minus minus"></i>
      </button>
    </section>
  );
};

export default CompCurrent;